const {
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableBody,
  TableCell,
  TableHeader,
} = DataTable;

class LogsPage extends Component {
  static contextTypes = {
    router: PropTypes.object,
  };
  state = {
    error: false,
    isSubmitted: false,
    wsname: null,
    content: "",
    isOpenAdd: false,
    isOpenEdit: false,
    current_id: '0',
  };

  componentDidMount() {
    const wsname = this.props.match.params.wsname;
    this.getData(wsname);
    this.setState({ wsname: wsname });
  }

  getData(wsname) {
    let url = `/api/log/${wsname}/`;
    console.log(url)
    this.props.axiosStore.instance
      .get(`${url}`)
      .then(response => {
        if (response.data.hasOwnProperty('content')) {
          this.setState({ content: response.data.content });
        } else {
          this.setState({ error: true });
        }
      })
      .catch(error => this.setState({ error: true }));
  }

  render() {
    const wsname = this.state.wsname;
    let content = this.state.content;
    let data = null;
    if (wsname != null) {
      //
      data = (
        <SyntaxHighlighter wrapLines={true} language="bash" style={vs2015}>
          {content}
        </SyntaxHighlighter>
      );
    }

    return (
      <div className="bx--grid bx--grid--full-width landing-page">
        <div className="bx--row landing-page__r2">
          <div className="bx--col bx--no-gutter">
            <div className="bx--grid bx--grid--no-gutter bx--grid--full-width">
              <div className="bx--row landing-page__tab-content">
                <div className="bx--col-md-4 bx--col-lg-16">
                  <h1 className="landing-page__heading">Logs Summary</h1>
                  <hr />
                </div>
                <div className="bx--col-md-4 bx--col-lg-16">{data}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default inject('sessStore', 'axiosStore')(observer(LogsPage));
